<template>
  <div>
    <v-progress-circular v-if="isLoading" indeterminate></v-progress-circular>
    <v-menu v-else offset-y>
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-on="on" v-bind="attrs">
          <display-avatar :member="member" />
        </v-btn>
      </template>
      <!--   -->
      <v-card>
        <v-sheet width="200">
          <member-menu v-if="member" v-model="dialog" @open="openDialog" />
          <no-member-menu v-else />
        </v-sheet>
      </v-card>
    </v-menu>
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn v-on v-bind="attrs" icon @click="setDarkMode(!darkMode)">
          <v-icon v-if="darkMode">mdi-lightbulb-on-outline</v-icon>
          <v-icon v-else>mdi-lightbulb-outline</v-icon>
        </v-btn>
      </template>
      <span>{{ darkMode ? " " : " " }}</span>
    </v-tooltip>

    <v-dialog v-if="member" v-model="dialog" persistent max-width="500">
      <v-card>
        <v-toolbar>
          <v-toolbar-title></v-toolbar-title>
          <v-spacer />
          <v-btn icon @click="closeDialog">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <user-update-form
          :member="member"
          :isLoading="isLoading"
          :cbCheckEmail="checkEmail"
          @onSave="save"
          @onLeave="leave"
        />
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import DisplayAvatar from "./DisplayAvatar.vue";
import UserUpdateForm from "../Auth/UserUpdateForm.vue";
import MemberMenu from "./MemberMenu.vue";
import NoMemberMenu from "./NoMemberMenu.vue";
export default {
  components: { DisplayAvatar, MemberMenu, NoMemberMenu, UserUpdateForm },
  name: "SiteUser",
  data() {
    return {
      isLoading: false,
      dialog: false,
    };
  },
  computed: {
    ...mapState({
      member: (state) => state.user.member,
    }),
    darkMode() {
      return this.$vuetify.theme.dark;
    },
  },
  mounted() {
    this.getDarkMode();
  },
  methods: {
    ...mapActions("user", [
      "duplicateCheck",
      "checkPassword",
      "updateMember",
      "signOut",
    ]),
    setDarkMode(mode) {
      // 
      localStorage.setItem("darkMode", mode ? "dark" : "light");
      this.$vuetify.theme.dark = mode;
    },
    getDarkMode() {
      const mode = localStorage.getItem("darkMode") === "dark" ? true : false;
      this.$vuetify.theme.dark = mode;
      //this.$vuetify.theme.dark = localStorage.getItem('darkMode') === 'dark' ? true : false;
    },

    async openDialog() {
      //this.dialog = true;
      if (!this.member.mb_provider) {
        const mb_password = await this.$ezNotify.prompt(
          " .",
          " ",
          { icon: "mdi-alert", formType: "password" }
        );

        if (mb_password) {
          this.dialog = await this.checkPassword({ mb_password });
        }
      } else {
        this.dialog = true;
      }
    },
    closeDialog() {
      this.dialog = false;
    },
    async save(form) {
      this.isLoading = true;
      const data = await this.updateMember(form);
      this.isLoading = false;
      if (data) {
        this.$toast.info(
          `${this.$store.state.user.member.mb_name}  .`
        );
        this.closeDialog();
        //this.$router.push("./login");
      }
    },
    async leave() {
      const result = await this.$ezNotify.confirm(
        " ?",
        "",
        {
          icon: "mdi-alert",
        }
      );
      if (!result) return;

      this.isLoading = true;

      const form = {
        mb_id: this.member.mb_id,
        mb_leave_at: this.$moment().format("LT"),
      };

      const data = await this.updateMember(form);
      this.isLoading = false;
      if (data) {
        //this.$toast.info(`${this.$store.state.user.member.mb_name} .`);
        this.closeDialog();
        const mb_name = await this.signOut();
        this.$toast.info(`${mb_name}  .`);
        if (this.$route.name != "Home") {
          this.$router.push("/");
        }
      }
    },
    async checkEmail(email) {
      const data = await this.duplicateCheck({
        field: "mb_email",
        value: email,
      });
      return data;
    },
  },
};
</script>

